}

.navigation {
  background-color: #c69963;
  grid-column: sidebar-start/sidebar-end;
  grid-row: 1/-1;
  display: flex;
  flex-direction: column;
  z-index: 1000;
}
@media only screen and (max-width: 62.5em) {
  .navigation {
    grid-row: 1/2;
    grid-column: 1/-1;
    position: sticky;
    top: 0;
    justify-content: center;
  }
}
.navigation__checkbox {
  width: 0;
  height: 0;
  visibility: hidden;
}
.navigation__button {
  width: 4.5rem;
  height: 3.3rem;
  position: sticky;
  top: 1.5rem;
  cursor: pointer;
  display: block;
  align-self: center;
  z-index: 1001;
}
@media only screen and (max-width: 62.5em) {
  .navigation__button {
    align-self: start;
    margin-left: 1.5rem;
    top: 0;
  }
}
.navigation__button:focus {
  outline: none;
}
.navigation__icon {
  border: none;
  border-radius: 0;
  background-color: #fff;
  height: 2px;
  width: 4.5rem;
  position: absolute;
  top: 1.5rem;
  line-height: normal;
  transition: all 0.3s;
}
.navigation__icon::before, .navigation__icon::after {
  background-color: #fff;
  height: 2px;
  width: 4.5rem;
  content: "";
  display: block;
  position: absolute;
  transition: all 0.3s;
}
.navigation__icon::before {
  transform: translateY(-1.5rem);
}
.navigation__icon:focus {
  outline: none;
}
.navigation__button:hover .navigation__icon, .navigation__button:focus .navigation__icon, .navigation__button:hover .navigation__icon::before, .navigation__button:focus .navigation__icon::before, .navigation__button:hover .navigation__icon::after, .navigation__button:focus .navigation__icon::after {
  background-color: #101d2c;
}
.navigation__checkbox:checked ~ .navigation__button .navigation__icon {
  background-color: transparent;
}
.navigation__checkbox:checked ~ .navigation__button .navigation__icon::before {
  top: 0;
  transform: rotate(135deg);
}
.navigation__checkbox:checked ~ .navigation__button .navigation__icon::after {
  top: 0;
  transform: rotate(-135deg);
}
.navigation__checkbox:checked + .navigation__menu {
  transform: translateX(0);
}
@media only screen and (max-width: 62.5em) {
  .navigation__checkbox:checked + .navigation__menu {
    transform: translateY(0);
  }
}
.navigation__checkbox:checked ~ .navigation__overlay {
  opacity: 1;
  visibility: visible;
}
.navigation__menu {
  height: 100vh;
  width: 16rem;
  background-color: #c69963;
  position: fixed;
  top: 0;
  transform: translateX(-100%);
  transition: all 0.3s ease-out;
  z-index: 1000;
}
@media only screen and (max-width: 62.5em) {
  .navigation__menu {
    height: auto;
    width: 100vw;
    transform: translateY(-100%);
    display: flex;
    justify-content: center;
  }
}
.navigation__links {
  margin-top: 8rem;
  font-size: 1.7rem;
}
@media only screen and (max-width: 62.5em) {
  .navigation__links {
    display: flex;
    justify-content: center;
    margin-top: 0;
    font-size: 2.5rem;
    align-items: center;
    width: 85%;
    flex-wrap: wrap;
  }
}
.navigation__item {
  list-style: none;
  margin: 2rem;
  margin-left: 1.75rem;
}
@media only screen and (max-width: 62.5em) {
  .navigation__item {
    margin: 1.25rem 2rem;
  }
}
.navigation__link {
  position: relative;
}
.navigation__link:link {
  color: #f9f7f6;
  text-decoration: none;
  text-transform: uppercase;
  font-family: "Josefin Sans", sans-serif;
  transition: all 0.2s;
}
.navigation__link:visited {
  color: #f9f7f6;
}
.navigation__link::after {
  content: "";
  display: block;
  height: 2px;
  width: 1px;
  position: absolute;
  bottom: -0.5rem;
  background-color: #f9f7f6;
  opacity: 0;
  transition: opacity 0.1s, width 0.3s ease-out;
}
.navigation__link:hover::after, .navigation__link:focus::after {
  opacity: 1;
  width: 100%;
}
.navigation__link:focus {
  outline: none;
}
.navigation__overlay {
  position: fixed;
  background-color: rgba(0, 0, 0, 0.8);
  width: 100vw;
  height: 100vh;
  top: 0;
  left: 0;
  z-index: 999;
  opacity: 0;
  visibility: hidden;
  transition: all 0.3s;
  cursor: pointer;
}
@supports (-webkit-backdrop-filter: blur(10px)) or (backdrop-filter: blur(10px)) {
  .navigation__overlay {
    -webkit-backdrop-filter: blur(10px);
    backdrop-filter: blur(10px);
    background-color: rgba(0, 0, 0, 0.3);
  }
}

